(function() {
  'use strict';

  angular
    .module('skbKonturTask')
    .directive('multiplicationMatrixBtn', multiplicationMatrixBtn);

  /** @ngInject */
  function multiplicationMatrixBtn() {
    return {
      restrict: 'E',
      controllerAs: 'vm',
      templateUrl: 'app/components/directiveMultiplicationMatrixBtn/multiplicationMatrixBtn.html',
      scope: {
        matrixes: '=',
        result: '=',
        matrixError: '=',
        disableMultiplyMatrix: '='
      },
      controller: multiplicationMatrixBtnCtrl,
      bindToController: true
    };

    /** @ngInject */
    function multiplicationMatrixBtnCtrl($scope,
                                         matrixService) {
      var vm = this;

      $scope.$watchCollection(function() {
        return vm.matrixes[0].content[0]
      }, function(newVal) {
        vm.matrixError = newVal.length !== vm.matrixes[1].content.length;
      });

      $scope.$watchCollection(function() {
        return vm.matrixes[1].content
      }, function(newVal) {
        vm.matrixError = newVal.length !== vm.matrixes[0].content[0].length;
      });

      vm.multiplyMatrix = function() {
        vm.result = matrixService.multiplyMatrix(vm.matrixes[0].content, vm.matrixes[1].content);
      }
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('skbKonturTask')
    .directive('matrix', matrix);

  /** @ngInject */
  function matrix() {
    return {
      restrict: 'E',
      controllerAs: 'vm',
      templateUrl: 'app/components/directiveMatrix/matrix.html',
      scope: {
        matrixes: '=',
        id: '=',
        focus: '=',
        disableMultiplyMatrix: "="
      },
      controller: matrixCtrl,
      bindToController: true
    };

    /** @ngInject */
    function matrixCtrl(_) {
      var vm = this,
          errorInputArr = {};

      vm.onFocus = function() {
        vm.focus = true;
      };

      vm.onBlur = function() {
        vm.focus = false;
      };

      vm.change = function(value, rowIndex, itemIndex) {
        var el = String(rowIndex) + String(itemIndex);

        if (value == undefined) {
          errorInputArr[el] = true;
          vm.disableMultiplyMatrix = true;
        } else {
          if (errorInputArr[el]) {
            delete errorInputArr[el];
          }
          if (_.size(errorInputArr) === 0) {
            vm.disableMultiplyMatrix = false;
          }
        }
      };
    }
  }
})();

